/**
 * Utilities
 * -----------------------------------------------------
 */
@import "../../../node_modules/bootstrap/scss/functions";
@import "../../../node_modules/bootstrap/scss/variables";
@import "../../../node_modules/bootstrap/scss/utilities";

$utilities: map-merge(
	$utilities,
	(
		// Font weight
		"font-weight": map-merge(
				map-get($utilities, "font-weight"),
			(
				values: map-merge(
					map-get(map-get($utilities, "font-weight"), "values"),
					(medium: 600),
				),
			),
		),
		// Opacity
		"opacity": (
			property: opacity,
			responsive: true,
			values: (
				0: 0,
				25: .25,
				50: .5,
				75: .75,
				100: 1,
			)
		),
		// Font-size
		"font-size": (
			property: "font-size",
			responsive: true,
			class: fz,
			values: (
				10: 10px,
				12: 12px,
				14: 14px,
				16: 16px,
				18: 18px,
			)
		),
		// Max-width
		"max-width": (
			property: max-width,
			responsive: true,
			class: mw,
			values: (
				25: 25%,
				50: 50%,
				75: 75%,
				100: 100%,
			)
		)
	)
);
